import React, {FC, useEffect, useRef, useState} from "react";
import {ChatMessageAPIType} from "../api/chat-api";
import {useDispatch, useSelector} from "react-redux";
import {sendMessage, startMessagesListening, stopMessagesListening} from "../redux/chat-reducer";
import {AnyAction} from "redux";
import {AppStateType} from "../redux/redux-store";
import {selectIsAuth} from "../redux/auth-selectors";


const ChatPage: FC = () => {
    const isAuth = useSelector(selectIsAuth)

    return <div>
        {isAuth
            ? <Chat/>
            : <AddMessageForm isAuth={isAuth}/>
        }
    </div>
}

const Chat: FC = () => {
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(startMessagesListening() as unknown as AnyAction)
        return () => {
            dispatch(stopMessagesListening() as unknown as AnyAction)
        }
    }, [dispatch])


    return <div>
        {status === 'error' && <div>Some error occurred. Please refresh page</div>}
        <>
            <Messages/>
            <AddMessageForm isAuth={true}/>
        </>
    </div>
}

const Messages: FC = () => {
    const messagesAnchorRef = useRef<HTMLDivElement>(null)
    const messages = useSelector((state: AppStateType) => state.chat.messages)
    const [isAutoScroll, setIsAutoScroll] = useState(false)

    const scrollHandler = (e: React.UIEvent<HTMLDivElement, UIEvent>) => {
        const element = e.currentTarget
        if (Math.abs((element.scrollHeight - element.scrollTop) - element.clientHeight) < 300) {
            !isAutoScroll && setIsAutoScroll(true)
        } else {
            isAutoScroll && setIsAutoScroll(false)
        }
    }

    useEffect(() => {
        if (isAutoScroll) {
            messagesAnchorRef.current?.scrollIntoView({behavior: "smooth"})
        }
    }, [messages])


    return <div style={{height: '500px', overflowY: 'auto'}} onScroll={scrollHandler}>
        {messages.map((m, index) => <Message key={m.id} message={m}/>)}
        <div ref={messagesAnchorRef}></div>
    </div>
}

const Message: FC<{ message: ChatMessageAPIType }> = React.memo(({message}) => {
    console.log('...message')
    return <div>
        <div style={{display: "flex", alignItems: "center"}}>
            <img alt={'user photo'} src={message.photo}/><b style={{marginLeft: '10px'}}>{message.userName}</b>
        </div>
        {message.message}
        <hr/>
    </div>
})

const AddMessageForm: FC<{ isAuth: boolean }> = ({isAuth}) => {

    const dispatch = useDispatch()

    const [message, setMessage] = useState('')

    const status = useSelector((state: AppStateType) => state.chat.status)


    const sendMessageHandler = () => {
        if (message) {
            dispatch(sendMessage(message) as unknown as AnyAction)
            setMessage('')
        }
    }

    return <div>
        <div>
            <textarea onChange={e => setMessage(e.currentTarget.value)} value={message}></textarea>
        </div>
        <div>
            <button disabled={status !== 'ready'} onClick={sendMessageHandler}>Send</button>
        </div>
        {isAuth
            ? ''
            : <h1><b>'Chat works only for login users!'</b></h1>
        }
    </div>
}

export default ChatPage